.row {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

@for $i from 1 through 12 {
    .col-#{$i}{
        width: 100% / 12 * $i;
    }
}

.row-center{
    display: flex;
    justify-content: center;
    align-items: center;
}

.row-height{
    background-color: #fff;
}